import { Component, OnInit, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { FavouriteService } from './../../services/favourite.service';
import { AdvertService } from "./../../services/advert.service";

import { NavController, LoadingController, AlertController, ToastController } from "@ionic/angular";
import { AuthService } from "../../services/auth.service";
import { ErrorService } from "../../services/error.service";
import { Urls } from "../../constants/urls";
import { combineLatest } from "rxjs";
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import * as copy from 'copy-to-clipboard';
import { CommentsComponent } from '../../components/comments/comments.component';
import { ImageService } from '../../services/image.service';
import { AdvertEntity, ImageModel, StatusEnum, UserModel, AdvertClient, MessageEntity, PaginationRequest, MessageClient, CreateMessageRequestModel, MessageTypeEnum, RatingClient, EntityTypeEnum } from '../../clients/bonobogive.client';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'advert',
    templateUrl: './advert.page.html',
    styleUrls: ['./advert.page.scss'],
})
export class AdvertPage implements OnInit {

    title = "Advert";
    advertPrimaryKey: string; // advert id
    advertPartitionKey: string; // advert id
    advertSortKey: string; // advert id
    advert: AdvertEntity;
    advertOwner: UserModel;
    // userInfo: UserInfo;
    canShare: boolean = false;
    copyUrlShare: boolean = false;
    advertComments: MessageEntity[] = [];
    commentsReady: boolean = false;
    postCommentForm: FormGroup;
    posting: boolean = false;
    lng: number;
    lat: number;
    rating: number;
    lastEvaluatedKeyHash: string;
    StatusEnum = StatusEnum;

    expiryDate: Date = new Date();
    @ViewChild('content') private content: any;
    @ViewChild(CommentsComponent) commentsComponent: CommentsComponent;
    // @ViewChild("commentinput") commentinput: Input;
    // @ViewChild("mapComponent") mapComponent: MapComponent;

    baseUrl: string = Urls.baseUrl;
    messageSpinner: boolean = false;
    advertDateCreated: string = "";
    body: string = "";
    expiresText: string = "Expires";

    advertImagesBySize: ImageModel[] = [];

    loadingAdvert = false;
    loadedAdvert = false;

    slideOpts = {
        initialSlide: 1,
        speed: 400
      };

    constructor(public navController: NavController,
        public auth: AuthService,
        public loadingCtrl: LoadingController,
        public alertCtrl: AlertController,
        public errorService: ErrorService,
        public advertService: AdvertService,
        private socialSharing: SocialSharing,
        private toastCtrl: ToastController,
        private formBuilder: FormBuilder,
        public favouriteService: FavouriteService,
        private activatedRoute: ActivatedRoute,
        public imageService: ImageService,
        private advertClient: AdvertClient,
        private commentClient: MessageClient,
        private ratingClient: RatingClient
    ) {
        this.lastEvaluatedKeyHash = undefined;
        this.advert = new AdvertEntity();
        // this.advert.address = new AddressModel();
        this.advert.images = [];
        //this.partitionKey = this.params.get("id");

        this.postCommentForm = this.formBuilder.group({
            body: ["", Validators.required]
        });

        this.postCommentForm = this.formBuilder.group({
            body: ["", Validators.required]
        });

        //Check if sharing via email is supported
        this.socialSharing.canShareViaEmail().then(() => {
            // Sharing via email is possible
            this.canShare = true;
        }).catch(() => {
            // Sharing via email is not possible
            this.canShare = false;
            this.copyUrlShare = true;
        });
    }

    openAdvertStatus() {
        this.navController.navigateForward("advert-status/" + this.advertPrimaryKey);
    }


    getUserRating(advert: AdvertEntity) {
        this.ratingClient.average(advert.userId, EntityTypeEnum.Advert).subscribe((result) => {
            if (result.averageRating) {
                this.rating = result.averageRating;
            }
            else {
                this.rating = 0;
            }

        });

    }

    async presentToast(message: string) {
        let toast = await this.toastCtrl.create({
            message: message,
            duration: 3000,
            position: "bottom"
        });

        await toast.present();
    }

    commentInputBlur() {
        this.advertService.showFooter = true;
    }

    commentInputFocus() {
        this.advertService.showFooter = false;
    }

    ngOnInit() {
        const urlParams = combineLatest(
            this.activatedRoute.params,
            this.activatedRoute.queryParams,
            (params, queryParams) => ({ ...params, ...queryParams })
        );

        urlParams.subscribe(routeParams => {
            this.advertPrimaryKey = routeParams.id;
            this.advertPartitionKey = this.advertPrimaryKey.split("|")[0];
            this.advertSortKey = this.advertPrimaryKey.split("|")[1];

            this.loadData().then(() => {
                console.log("Advert " + this.advertPrimaryKey + " loaded");
            });
        });
    }

    async loadData() {
        this.advertComments = [];
        await this.getAdvert(this.advertPartitionKey, this.advertSortKey);
        this.getAdvertComments();
    }

    async refresh(refresher) {
        this.advertComments = [];
        await this.loadData();
        refresher.target.complete();
    }

    copyUrl() {

        var currentUrl = window.location.href;
        copy(currentUrl, {
            debug: true,
            message: 'Press #{key} to copy'
        });

        this.presentToast("Link copied");
    }

    async share() {
        var result = await this.socialSharing.share(`I found this on Bonobo Give... ${this.advert.title} I found this on Bonobo Give... ${this.advert.title}`, null, `${Urls.baseUrlLive}advert/${this.advert.partitionKey}`);
        console.log(result);
    }


    copyShareUrl() {

        var urlToCopy = Urls.baseUrlLive + "/#/advert/" + this.advert.partitionKey;
        var element = document.createElement("input");

        //Assign different attributes to the element.
        element.setAttribute("type", "hidden");
        element.setAttribute("value", urlToCopy);
        element.setAttribute("name", "shareUrl");
        element.setAttribute("id", "shareUrl");
        document.body.appendChild(element);
        element.select();
        var successful = document.execCommand("copy");
        if (successful === true) {
            this.presentToast("Link copied");
        }
        else {
            this.presentToast("Failed to copy share url");
        }
    }

    deleteFavourite() {
        this.favouriteService.deleteFavouriteByAdvertEntity(this.advert).subscribe(res => {

        }, err => {
            console.log(err);
        });;
    }

    async deleteAdvert(advert: AdvertEntity) {
        await this.advertClient.delete(advert.partitionKey, advert.sortKey);
        this.navController.navigateForward("my-adverts");
    }

    async addFavourite() {
        await this.favouriteService.addFavourite(this.advertPrimaryKey);
    }

    @ViewChild('commentinput', { read: ElementRef }) input: ElementRef;
    focusOnCommentInput() {
        setTimeout(() => {
            this.input.nativeElement.setFocus();
        });
    }

    openEditPage() {
        this.navController.navigateForward("post/" + this.advertPrimaryKey);
    }

    openUserProfile(userId: string) {
        this.navController.navigateForward("user-profile/" + userId);
    }

    async getAdvert(advertPartitionKey: string, advertSortKey: string) {
        this.loadingAdvert = true;
        this.loadedAdvert = false;
        this.advertClient.get(advertPartitionKey, advertSortKey).subscribe(async res => {
            this.advert = res;
        }, error => {
            console.log("Error loading advert", error);
            this.navController.navigateForward("home");
            this.presentToast("Sorry there was an issue loading this advert");
        })
        .add(()=>{
            this.loadingAdvert = false;
            this.loadedAdvert = true;
        });
    }



    addDays(date, days) {
        var result = new Date(date);
        result.setDate(result.getDate() + days);
        return result;
    }

    private scrollToBottom() {
        setTimeout(() => {
            try {
                if (this.content) { this.content.scrollToBottom(); }
            }
            catch (e) {

            }
        });
    }

    async postComment() {
        this.posting = true;
        let request = new CreateMessageRequestModel();
        request.entityPrimaryKey = this.advertPrimaryKey;
        request.messageType = MessageTypeEnum.Comment;
        request.text = this.postCommentForm.value["body"];
        request.userId = this.auth.user.uid;

        this.commentClient.post(request).subscribe(async () => {
            this.postCommentForm.reset();
            this.posting = false;
            this.scrollToBottom();
            await this.loadData();
        });
    }



    getAdvertComments() {
        console.log("Getting advert comments");
        var pagination = new PaginationRequest();
        pagination.take = 10;
        pagination.nextRequestBlob = this.lastEvaluatedKeyHash;
        this.advertComments = [];
        this.commentClient.get(this.advertPrimaryKey, MessageTypeEnum.Comment, pagination.take, pagination.nextRequestBlob).subscribe(res => {
            this.advertComments.push(...res.messages);
        });
    }

    goToLogin() {
        this.navController.navigateForward("login");
    }

    loadReportPage() {
        this.navController.navigateForward(`report/${this.advertPrimaryKey}`);
    }


}
